.container-contact{
        height: 93vh;
        color: white;
        padding:0;
        background-color:rgba(0,0,0,0.6);
        box-shadow: 8px 8px 6px 6px darkslategray;
        overflow: hidden;
        margin:0.5em 7em;
        border-radius: 2em;

        
}
#text-heading{
    background-color: rgba(0,0,0,0.5);
    margin:.0 11em;
    padding: 0%;
    border-radius: 2rem;
    
}
.massage{
    margin: 1em 20rem;
    border-radius: 4rem;
    font-weight: bold;
}
level{
    display: flex;
    justify-content: center;
    margin-top: 2em;
    font-weight: bold;
}
input{
    border-radius: .5rem;
    padding: .4rem 0;
}
button{
    padding: 0 1em;
    border-radius: .5rem;
    background-color: cornflowerblue;
}