body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.main_header{
   background-image: url(/static/media/background.8e55313c.jpg);
   background-color:rgb(99, 90, 99);
   height:93vh;
}

.welcome{
   font-size:5rem; 
   position: absolute;
   left: 7em;
   color: rgba(34, 33, 33, 0.836);
   
}
.img{
   width: 28rem;
   height:28rem;
   border-radius:50%;
   position: relative;
   bottom: 0;
   right:29rem;
}
.name{
   position: absolute;
   bottom:14em;
   right:17rem;
}


*{
    margin: 0;
    padding: 0;
}
table, th, td {
    border: 1px solid white;
    padding:1em 6em;
  }
  table{
      margin:0 17%;
  }
 
.container{
    height: 93vh;
    margin-top: 2px;
    margin-bottom: 2px;
    width: 100rem;
    border-radius: 18px;
    background-color: rgba(0,0,0,0.6);
    box-shadow: 8px 8px 6px 6px darkslategray;
}
.container-contact{
        height: 93vh;
        color: white;
        padding:0;
        background-color:rgba(0,0,0,0.6);
        box-shadow: 8px 8px 6px 6px darkslategray;
        overflow: hidden;
        margin:0.5em 7em;
        border-radius: 2em;

        
}
#text-heading{
    background-color: rgba(0,0,0,0.5);
    margin:.0 11em;
    padding: 0%;
    border-radius: 2rem;
    
}
.massage{
    margin: 1em 20rem;
    border-radius: 4rem;
    font-weight: bold;
}
level{
    display: flex;
    justify-content: center;
    margin-top: 2em;
    font-weight: bold;
}
input{
    border-radius: .5rem;
    padding: .4rem 0;
}
button{
    padding: 0 1em;
    border-radius: .5rem;
    background-color: cornflowerblue;
}
*{
  margin: 0%;
  padding: 0%;
}  
.App {
  text-align: center;
  background-image: url(/static/media/background.8e55313c.jpg);
}
.fa-facebook {
  background: #3B5998;
  color: white;
}

.fa-twitter {
  background: #55ACEE;
  color: white;
}
.fa-linkedin {
  background: #007bb5;
  color: white;
}
.fa {
  padding-top:0.5em;
  font-size: 15px;
  width: 2em;
  height: 2em;
  text-align: center;
  text-decoration: none;
  margin: 1em 2em;
  border-radius: 20%;
  background-image:linear-gradient(red,yellow);
}
.material-icons{
  font-size:2em;
}


